<template>
  <div>
    <van-search
      v-model.trim.lazy="value"
      placeholder="输入内容"
      left-icon=""
      right-icon=""
      shape="round"
    />
    <div v-show="!value" class="ranking">
      <h2>热门</h2>
      <div
        v-for="(item, index) of arrs"
        :key="item.id"
        @click="$bus.$emit('details', item.id)"
      >
        <span>
          {{ index + 1 }}
        </span>
        <span>
          {{ item.name }}
        </span>
      </div>
    </div>

    <div v-show="value" class="gl">
      <div
        v-for="item of Results"
        :key="item.id"
        @click="$bus.$emit('details', item.id)"
        class="searchcontent"
      >
        <div class="searchcontent-s">
          <p>{{ item.name }}</p>
          <p>{{ item.author }}</p>
        </div>
        <div class="searchcontent-x">
          <img :src="item.cover_image" />
          <div>
            <p>{{ item.name }}</p>
            <p>{{ item.intro }}</p>
            <p>{{ item.author }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
  
  <script>
export default {
  name: "Mysearch",
  components: {},
  computed: {
    arrs() {
      return this.hot_top.splice(0, 10);
    },
  },
  data() {
    return {
      hot_top: [],
      value: "",
      Results: [],
    };
  },
  mounted() {
    this.axios.get("https://jyapi.jyacg.com/web/index").then((res) => {
      if (res.status == 200) {
        this.hot_top = res.data.data.hot_top;
      }
    });
  },
  watch: {
    value() {
      if (this.value == "") {
        this.Results = "";
        return;
      }
      this.axios
        .get(
          `https://jyapi.jyacg.com/web/search?name=${this.value}&type=1&page=1&limit=10`
        )
        .then((res) => {
          if (res.status == 200) {
            this.Results = res.data.data;
          }
        });
    },
  },
};
</script>
<style lang="scss">
.van-field__control::-webkit-input-placeholder {
  color: #757598;
}
.van-search__content {
  background-color: #e6e6e6;
}
.ranking {
  width: 340px;
  margin: 25px 17.6px 0;
  h2 {
    font-size: 15px;
    color: #999;
    font-weight: bold;
    margin-bottom: 15px;
  }
  div {
    width: 100%;
    margin-bottom: 15px;
    span:nth-child(1) {
      display: inline-block;
      border: 1px solid #999;
      background-color: #999;
      color: #fff;
      text-align: center;
      width: 20px;
      height: 20px;
      font-size: 13px;
      border-radius: 5px;
      line-height: 20px;
      margin-right: 10px;
    }
    span:nth-child(2) {
      color: #999;
      font-size: 13px;
    }
  }
  div:nth-child(2) {
    span:nth-child(1) {
      background-color: #f90;
    }
  }
  div:nth-child(3) {
    span:nth-child(1) {
      background-color: #fc3;
    }
  }
  div:nth-child(4) {
    span:nth-child(1) {
      background-color: #c33;
    }
  }
}
.ranking,.gl{
  min-height: 583px;
}
.searchcontent {
  width: 300px;
  margin: 0 37.5px;
  
  .searchcontent-s{
    margin: 30px 0 15px;
    border-bottom: 1px solid #e4e4e4;
    p{
      margin:  0 0  15px 0;
      font-size: 13px;
    }
  }
  .searchcontent-x{
    display: flex;
    img{
      width: 70px;
      margin-right: 10px;
    }
    div{
      margin-top: 10px;
      p{
        margin-bottom: 8px;
        line-height: 15px;
        overflow: hidden;
        color: #999;
      }p:nth-child(1){
        color: #000;
      }
      p:nth-child(2){
        line-height: 20px;
        height: 40px;
      }
    }
  }
}
</style>